section{
    display: grid; 
    grid-template-columns: 0.8fr 1.1fr 1.1fr; 
    grid-template-rows: 0.5fr 3.6fr 0.3fr; 
    gap: 0px 0px; 
    grid-template-areas: 
    "header header header"
    "main main main"
    "main main main"; 
    width: 100%; 
    height: 100%; 
    background:rgba(27, 27, 27, 0.5) none no-repeat scroll 0 0;
}

header{
    grid-area: header;
}

main{
    grid-area: main;
    overflow-y: auto;
}

